import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Ingredient } from 'src/app/shared/ingredient.model';
import { RecipeService } from '../recipe.service';

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styleUrls: ['./recipe-edit.component.scss']
})
export class RecipeEditComponent implements OnInit {
  id: number;
  id_be: number;
  ingredientIds = [];
  editMode = false;
  recipeForm: FormGroup;
  ingredientIdsToDelete = [];
  ingredients: Ingredient[] = [];

  constructor(private route: ActivatedRoute, private recipeService: RecipeService, private router: Router) { }

  ngOnInit(): void {
    this.route.params
      .subscribe(
        (params: Params) => {
          this.id = +params['id'];
          this.editMode = params['id'] != null;
          this.initForm();
        }
      )
  }

  onSubmit() {
    // const newRecipe = new Recipe(
    //   this.recipeForm.value['name'],
    //   this.recipeForm.value['description'],
    //   this.recipeForm.value['imagePath'],
    //   this.recipeForm.value['ingredients']
    //   );
    if (this.editMode) {
      this.recipeService.updateRecipe(this.id, this.id_be, this.ingredientIds, this.recipeForm.value);
      if (this.ingredientIdsToDelete.length > 0) this.recipeService.deleteIngredients(this.ingredientIdsToDelete);
    } else {
      this.recipeService.saveRecipe(this.recipeForm.value);
    }
    this.onCancel();
  }

  onAddIngredient() {
    (<FormArray>this.recipeForm.get('ingredients')).push(
      new FormGroup({
        'name': new FormControl(null, Validators.required),
        'amount': new FormControl(null, [
          Validators.required,
          Validators.pattern(/^[1-9]+[0-9]*$/)
        ])
      })
    );
  }

  private initForm() {
    let recipeName = '';
    let recipeImagePath = '';
    let recipeDescription = '';
    let recipeIngredients = new FormArray([]);

    if (this.editMode) {
      const recipe = this.recipeService.getRecipeById(this.id);
      console.log('recipe: ', recipe);
      if (recipe.ingredients.length > 0) {
        recipe.ingredients.forEach(ingredient => this.ingredientIds.push(ingredient.id));
        this.ingredients = recipe.ingredients;
        console.log('ids', this.ingredientIds);

      }
      this.id_be = recipe.id;
      recipeName = recipe.name;
      recipeImagePath = recipe.imagePath;
      recipeDescription = recipe.description;
      if (recipe['ingredients']) {
        for (let ingredient of recipe.ingredients) {
          recipeIngredients.push(
            new FormGroup({
              'name' : new FormControl(ingredient.name, Validators.required),
              'amount' : new FormControl(ingredient.amount, [
                Validators.required,
                Validators.pattern(/^[1-9]+[0-9]*$/)
              ]),
              'id': new FormControl(ingredient.id)
            })
          );
        }
      }
    }

    this.recipeForm = new FormGroup({
      'name': new FormControl(recipeName, Validators.required),
      'imagePath': new FormControl(recipeImagePath, Validators.required),
      'description': new FormControl(recipeDescription, Validators.required),
      'ingredients': recipeIngredients
    });
  }

  onCancel() {
      this.router.navigate(['../'], {relativeTo: this.route});
  }

  onDeleteIngredient(index: number) {
    console.log('index', index);
    (<FormArray>this.recipeForm.get('ingredients')).removeAt(index);
    if (index + 1 <= this.ingredients.length && this.ingredients[index].id) {
      this.ingredientIdsToDelete.push(this.ingredients[index].id);
      this.ingredients.splice(index, 1);
      this.ingredientIds.splice(index, 1);
    }
    // if (this.ingredientIds.length > 0 && index + 1 <= this.ingredientIds.length) {
    //   // this.recipeService.deleteIngredient(this.ingredientIds[index]);
    //   this.ingredientIds.splice(index, 1);
    // }
  }

  get controls() {
    return (this.recipeForm.get('ingredients') as FormArray).controls;
  }

}
